.main-section .tour-box {


.btn-margin-right {
  margin-right: 40px;
}
  .trip-featured-img-list {
    float: none;
    margin: 20px auto;
    width: 95%;
    border-radius: 3px;
    padding: 1px;
    max-width: none;
    max-height: auto;
    height: auto;
    border: 1px black solid;

    div {
      width: 100%;
      height: 320px;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;
      border-radius: 3px;

    }
  }
  }

.tour-box {
  h2 {
    font-size: 2em;
    font-weight: bold;
  }
}

.larger {
  font-size: 1.15em;
  font-weight: bold;
}

.summary-text {
  margin-top: 15px;
}
